.odc-functions-getting-started-section {
  padding-right: var(--pf-v5-global--spacer--lg);
  padding-left: var(--pf-v5-global--spacer--lg);
}

.pf-v5-c-expandable-section__toggle-text {
  color: var(--pf-v5-global--Color--dark-100) !important;
  font-size: var(--pf-v5-global--icon--FontSize--md) !important;
  font-weight: var(--pf-v5-global--FontWeight--bold) !important;
}

.pf-v5-c-expandable-section__toggle-text.is-dark {
  color: var(--pf-v5-global--Color--light-100) !important;
}

.pf-v5-c-expandable-section__toggle {
  align-items: center;
}

.pf-v5-c-expandable-section__content {
  padding-bottom: 0;
}

.odc-functions-getting-started-grid {
  --min-column-width: 220px;
  box-shadow: none;
}

.odc-functions-getting-started-grid__header.pf-v5-c-card__header {
  padding-right: var(--pf-v5-global--spacer--sm);
}

.odc-functions-getting-started-grid__tooltip {
  white-space: pre-line;
}

.odc-functions-getting-started-grid__tooltip-icon {
  padding-left: var(--pf-v5-global--spacer--sm);
}

.odc-functions-getting-started-grid__content.pf-v5-c-card__body {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(var(--min-column-width), 1fr));
  padding: 0 0 calc(var(--pf-v5-c-card--child--PaddingBottom)/2) 0;
  overflow: hidden;
}

.odc-functions-getting-started-grid__content.pf-v5-c-card__body>.pf-v5-l-flex.pf-m-column {
  border-right: var(--pf-v5-global--BorderWidth--sm) solid var(--pf-v5-global--BorderColor--100);
  margin-right: calc(-1*var(--pf-v5-global--BorderWidth--sm));
  padding-top: calc(var(--pf-v5-c-card--first-child--PaddingTop)/2);
  padding-bottom: calc(var(--pf-v5-c-card--child--PaddingBottom)/2);
  padding-left: var(--pf-v5-global--spacer--md);
  padding-right: var(--pf-v5-c-card--child--PaddingRight);
}

